                Modifier
                    .fillMaxWidth()
                    .verticalScroll(scrollState)
                    .hazeSource(haze)
            ) {
                Box(
                    Modifier.height(screenHeight),
                    contentAlignment = Alignment.Center
                ) {
                    Image(
                        painterResource(Res.drawable.bg), null,
                        Modifier
                            .fillMaxSize()
