                                    "\n" +
                                    "As a dynamic newcomer to the industry, we are actively exploring novel gameplay mechanics for mobile platforms, integrating lightweight AI technology and intuitive interaction design to deliver versatile entertainment solutions. Moving forward, we remain committed to bridging creativity with technical excellence, crafting \"small yet brilliant\" digital surprises for global players.",
                            style = MaterialTheme.typography.titleLarge
                        )
                        Spacer(Modifier.weight(1f))
                    }
                }
                Box(
                    Modifier.height(screenHeight),
                    contentAlignment = Alignment.CenterEnd
                ) {
                    if (screenWidth > 720.dp) {
                        Image(
                            painterResource(Res.drawable.ftr), null,
                            Modifier
                                .align(Alignment.CenterStart)
                                .size(screenWidth - 720.dp, screenHeight)
                                .padding(64.dp)
